<template>
  <v-container>
    <v-card class="transparent numberbg mx-auto" max-width="600">
      <v-card-title>
        <h2 class="mx-auto">Identify Yourself</h2>
      </v-card-title>
      <v-card-text>
        <div class="my-2">
          <v-select
            label="Select Course"
            :items="authoptions"
            outlined
            dense
            v-model="course"
          >
          </v-select>
        </div>
        <div class="my-2">
          <p
            class="text-center kalpurush"
            style="color: black; font-size: 17px"
          >
            {{ this.course == "mathhaters" ? "Math Haters" : "Apar Chinmoy" }}
            Admission Course এ টাকা পাঠানোর সময় যে
            {{ this.course == "mathhaters" ? "ফোন নম্বর" : "ইমেইল" }} ব্যবহার
            করেছিলে এইখানেও একই
            {{ this.course == "mathhaters" ? "ফোন নম্বর" : "ইমেইল" }} ব্যবহার
            করে প্রবেশ করবে।
          </p>
        </div>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-if="course == 'aparchinmoy'"
            v-model="cred"
            type="email"
            label="Email"
            placeholder="Email"
            :rules="emailRules"
          />
          <v-text-field
            v-if="course == 'mathhaters'"
            v-model="phone"
            label="Mobile No."
            placeholder="Mobile No."
            prefix="+880"
            :rules="[
              (v) => !!v || 'Phone number is required',
              (v) =>
                (/\d{10}/.test(v) && v.length == 10) ||
                'Phone number must be valid',
            ]"
          />
          <v-text-field
            v-if="course == 'aparchinmoy'"
            v-model="password"
            type="password"
            label="Group Joining ID"
            placeholder="Password"
          />
          <v-btn outlined to="/resetpass" color="primary">
            Forget Password?</v-btn
          >
          <div v-if="unknown">
            <v-text-field
              v-model="name"
              label="Name"
              placeholder="Name"
              :rules="[(v) => !!v || 'Name is required']"
            />

            <v-text-field
              v-if="course == 'aparchinmoy'"
              v-model="phone"
              label="Mobile No."
              placeholder="Mobile No."
              prefix="+880"
              :rules="[
                (v) => !!v || 'Phone number is required',
                (v) => /\d{10}/.test(v) || 'Phone number must be valid',
              ]"
            />
            <v-text-field
              v-model="school"
              label="College Name"
              placeholder="College Name"
              :rules="[(v) => !!v || 'College name is required']"
            />
            <v-select
              v-model="gender"
              :items="['Male', 'Female']"
              label="Gender"
              :rules="[(v) => !!v || 'Gender is required']"
            />

            <v-select
              :items="[
                'Dhaka',
                'Mymensingh',
                'Chattagram',
                'Rajshahi',
                'Barishal',
                'Syhlet',
                'Rangpur',
                'Khulna',
              ]"
              label="Division"
              v-model="division"
              :rules="[(v) => !!v || 'Division is required']"
            />
          </div>
          <v-sheet
            color="success"
            class="pa-3"
            dark
            v-if="this.$store.state.error"
            >{{ this.$store.state.error }}</v-sheet
          >
        </v-form>
        <v-alert class="error" dark v-if="message"> {{ message }} </v-alert>
      </v-card-text>
      <v-card-actions>
        <v-btn
          v-if="!unknown"
          @click="checkPhoneNumber"
          tile
          large
          class="primary ma-auto"
          :loading="loading"
          >Submit</v-btn
        >
        <v-btn
          v-else
          @click="submitInformation"
          tile
          large
          class="primary ma-auto"
          :loading="loading"
          >Submit</v-btn
        >
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
import { auth } from "@/plugins/config.js";
export default {
  data() {
    return {
      authoptions: [
        { text: "Apar Chinmoy Physics Course", value: "aparchinmoy" },
        { text: "Math Haters Math Course", value: "mathhaters" },
      ],
      course: "aparchinmoy",
      valid: true,
      loading: false,
      phone: "",
      name: "",
      cred: "",
      gender: "",
      school: "",
      division: "",
      password: "",
      unknown: false,
      message: "",
      emailRules: [
        (v) =>
          /^(([^<>()[\]\\.,;:\s@\\"]+(\.[^<>()[\]\\.,;:\s@\\"]+)*)|(\\".+\\"))@(([^<>()[\]\\.,;:\s@\\"]+\.)+[^<>()[\]\\.,;:\s@\\"]{2,})$/i.test(
            v
          ) || "Email must be valid",
      ],
    };
  },
  methods: {
    checkPhoneNumber() {
      if (this.$refs.form.validate()) {
        this.loading = !this.loading;
        if (this.course == "mathhaters") {
          fetch(
            "https://script.google.com/macros/s/AKfycbxkUJ0ib6MbkwYUnnOEJgmiKQknB8zauTrrQQuA89o1mZP7iuoJkj_fdlzlnHzmRuHs/exec?phone=" +
              this.phone
          )
            .then((res) => res.json())
            .then((data) => {
              let cred = this.phone.substr(this.phone.length - 10) + "@ac.com";
              if (data.message == "exists") {
                fetch(
                  auth +
                    `?phone=${this.phone.substr(
                      this.phone.length - 10
                    )}&check=phone`
                )
                  .then((res) => res.json())
                  .then((data) => {
                    this.loading = !this.loading;
                    if (data.message == "exists") {
                      localStorage.setItem("acuser", cred);
                      this.$router.push("/");
                    } else {
                      this.unknown = !this.unknown;
                    }
                  });
              } else {
                this.loading = !this.loading;
                this.message = "Doesn't exists.";
              }
            });
        } else {
          fetch(auth + `?email=${this.cred}&check=email`)
            .then((res) => res.json())
            .then((data) => {
              if (data.message == "exists") {
                this.login();
              } else {
                this.loading = !this.loading;
                this.unknown = !this.unknown;
              }
            });
        }
      }
    },
    submitInformation() {
      let data = localStorage.getItem("acuser");
      if (this.$refs.form.validate() && !data) {
        this.loading = !this.loading;
        let cred = this.cred
          ? this.cred
          : this.phone.substr(this.phone.length - 10) + "@ac.com";
        fetch(auth, {
          method: "POST",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify([
            cred,
            this.name,
            this.school,
            this.phone.substr(this.phone.length - 10),
            this.gender,
            this.division,
          ]),
        })
          .then((res) => res.json())
          .then(() => {
            if (this.course == "mathhaters") {
              localStorage.setItem("acuser", cred);
              this.$router.push("/");
            } else {
              this.login();
            }

            this.loading = !this.loading;
          });
      }
    },
    login() {
      if (/[\d]{11}/g.test(this.cred) && this.password) {
        this.$store.commit("SET_ERROR", "");
        let userdata = {
          cred: this.cred + "@asg.com",
          password: this.password,
        };
        this.$store.dispatch("userLogin", userdata);
      } else if (
        /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i.test(
          this.cred
        ) &&
        this.password
      ) {
        this.$store.commit("SET_ERROR", "");

        let userdata = {
          cred: this.cred,
          password: this.password,
        };
        this.$store.dispatch("userLogin", userdata);
        this.loading = !this.loading;
      } else {
        this.$store.commit("SET_ERROR", "Please provide valid credential.");
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.numberbg {
  background: rgba(255, 255, 255, 0.7) !important;
  border-radius: 35px;
}
</style>